import axios from 'axios';
import {SUCC_CODE, TIMEOUT} from './config';

const CancelToken = axios.CancelToken;
let cancel;

export const getCategoryContent = (id) => {
  cancel && cancel('åæ¶äºåä¸æ¬¡çè¯·æ±!');

  return axios.get(`http://www.imooc.com/api/category/content/${id}`, {
    timeout: TIMEOUT,
    cancelToken: new CancelToken(function (c) {
      cancel = c;
    })
  }).then(res => {
    if (res.data.code === SUCC_CODE) {
      // è¯·æ±æåcancelä¸ºNull
      cancel = null;
      return res.data;
    }

    throw new Error('æªè½è·åå°æ°æ®');
  }).catch(err => {
    if (axios.isCancel(err)) { // åæ¶åä¸æ¬¡çè¯·æ±
      console.log(err);
    } else {
      console.log(err);
    }
  });
};



